* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url("https://github.com/mooyeon-choi/trello_clone_coding/blob/master/public/images/background.jpg?raw=true");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

/* nav */
.navbar {
  position: fixed;
  width: 100%;
  font-size: 1rem;
  padding: 10px;
  height: 5vh;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.6);
}

.navbar-logo {
  opacity: 0.6;
  height: 100%;
}

.navbar-logo:hover {
  opacity: 1;
  height: 100%;
  cursor: pointer;
}

/* board */
.flexbox {
  display: flex;
  justify-content: flex-start;

  width: 100%;
  height: 100vh;

  overflow: auto;

  margin: 0 auto;
  padding: 60px 15px 15px 15px;
}

.board {
  width: 100%;
  max-width: 280px;
  padding: 5px;
}

.list {
  display: flex;
  flex-direction: column;

  width: 100%;
  min-width: 250px;
  min-height: 40px;

  background-color: rgb(20, 20, 20);

  border-radius: 5px;

  margin-right: 15px;
  padding: 0;
}

.add-list {
  width: 100%;
  min-width: 250px;
  min-height: 45px;

  background-color: rgb(20, 20, 20);
  opacity: 0.6;

  border-radius: 5px;
  margin-right: 15px;
  padding: 10px 20px;

  font-size: 1rem;
  color: white;
}

.add-list:hover {
  opacity: 0.8;
  cursor: pointer;
}

.add-icon {
  margin-right: 10px;
}

.add-list-form {
  width: 100%;
  min-width: 250px;
  min-height: 45px;

  background-color: rgb(20, 20, 20);

  border-radius: 5px;
  padding: 8px;

  font-size: 1rem;
  color: white;
}

.add-list-input {
  width: 100%;
  min-height: 30px;
  resize: none;

  border-radius: 5px;
  border: 0;
  outline: 0;

  padding: 5px 10px;
  margin-bottom: 10px;
}

.list-name-space {
  display: flex;
  justify-content: space-between;

  width: 100%;
  height: 40px;
  color: white;
}

.list-name {
  width: 100%;
  height: 40px;
  padding: 15px 10px 20px 20px;
}

.update-list-form {
  width: 100%;
  height: 40px;
  color: white;
  padding: 10px;
}

.list-setting {
  height: 40px;
  width: 50px;

  border: 0;
  border-radius: 3px;

  background-color: transparent;
  text-align: center;
  font-size: 1.3rem;
  color: white;

  padding: 8px 10px 20px 0;

  user-select: none;
}

.list-setting:hover {
  cursor: pointer;
  background-color: #222222;
}

.list-setting-items {
  position: relative;
  list-style: none;
  z-index: 100;
  width: 230px;

  cursor: default;
  border-radius: 4px;
  box-shadow: 0 1px 10px black;
  margin-top: 5px;
  padding: 5px;

  background-color: white;
  color: rgb(20, 20, 20);
}

.list-setting-title {
  margin: 5px;
  font-size: 0.9rem;
}

.list-setting-item {
  margin: 10px;
  text-align: left;
  font-size: 0.9rem;
}

.list-setting-item:hover {
  cursor: pointer;
  opacity: 0.6;
}

.list-setting-background {
  position: fixed;
  z-index: 90;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  opacity: 0;

  cursor: default;
}

.card {
  padding: 5px 10px;
  min-height: 35px;
  background-color: #f3f3f3;

  border-radius: 5px;

  cursor: pointer;
  margin: 5px 10px;
}

.add-another-card {
  width: 100%;
  min-height: 40px;

  padding: 0 5px 5px;

  font-size: 1rem;
  color: white;
}

.add-another-card-button {
  width: 100%;
  height: 100%;

  opacity: 0.6;

  border-radius: 5px;
  padding: 6px 6px 6px 17px;

  font-size: 1rem;
  color: white;
}

.add-another-card-button:hover {
  background-color: #222222;
  opacity: 1;
  cursor: pointer;
}

.add-card-form {
  padding: 5px;
}

.add-card-input {
  width: 100%;
  min-height: 70px;
  resize: none;

  border-radius: 5px;
  border: 0;
  outline: 0;

  padding: 5px 10px;
  margin-bottom: 5px;
}

.add-card-button {
  width: 90px;
  min-height: 30px;

  border-radius: 5px;
  border: 0;
  outline: 0;

  background-color: #33ad0a;
  font-size: 0.9rem;
  color: white;
}

.add-card-button:hover {
  cursor: pointer;
  background-color: #37ba0b;
}

.add-card-cancel-button {
  width: 30px;
  height: 30px;

  margin-left: 5px;
  border-radius: 5px;
  border: 0;
  outline: 0;

  background-color: transparent;
  font-size: 1rem;
  color: white;
}

.add-card-cancel-button:hover {
  cursor: pointer;
  background-color: #2f2f2f;
}

/* hiding scrollbar */
::-webkit-scrollbar {
  display: none;
}
